import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import InputBox from "../../components/InputBox/index";
import LongBtn from "../../components/Button/LongBtn";
import ProfileImg from "../../components/ImageBox";
import { useNavigate, useLocation } from "react-router-dom";
import defaultUserImg from "../../assets/images/user_img_big.svg";
import axios from "axios";
import * as S from "./style";

const SetProfile = () => {
  const [username, setUsername] = useState("");
  const [accountname, setAccountname] = useState("");
  const [itemIntro, setItemIntro] = useState("");
  const [usernameError, setUsernameError] = useState("");
  const [accountnameError, setAccountnameError] = useState("");

  const [usernameValid, setUsernameValid] = useState(false);
  const [accountnameValid, setAccountnameValid] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();
  const email = location.state.email;
  const password = location.state.password;
  const passed = username && accountname;

  // const [image, setImage] = useState(`${userImg}`);

  /*     */
  useEffect(() => {
    const usernameRegex = /^([-|-|a-z|A-Z|0-9|]){2,10}$/;

    if (!usernameRegex.test(username)) {
      setUsernameError("*    ");
      setUsernameValid(false);
    } else {
      setUsernameError("");
      setUsernameValid(true);
    }
  }, [username]);

  /* ID    */
  useEffect(() => {
    const accountnameRegex = /^([A-Za-z0-9_.])+$/;
    if (!accountnameRegex.test(accountname) && accountname !== "") {
      setAccountnameError("*, ,      .");
      setAccountnameValid(false);
    } else if (accountname === "") {
      setAccountnameValid(false);
    } else {
      setAccountnameError("");
      setAccountnameValid(true);
    }
  }, [accountname]);

  /*   */
  const [userImage, setUserImage] = useState("");

  /*  */
  const signInHandler = async () => {
    try {
      const res = await axios.post(`${process.env.REACT_APP_API_KEY}/user`, {
        headers: {
          "Content-type": "application/json",
        },
        user: {
          username: username,
          accountname: accountname,
          intro: itemIntro,
          email: email,
          password: password,
          image: userImage,
        },
      });

      navigate("/emaillogin");
    } catch (error) {
      if (error.response.data.message === "   ID.") {
        setAccountnameError("   ID.");
      }
    }
  };

  return (
    <S.FormContainer>
      <S.PageTitle className="ir"> </S.PageTitle>
      <S.ProfileEditTitle> </S.ProfileEditTitle>
      <S.TitleExplain>    .</S.TitleExplain>
      <S.ImgWrapper>
        <ProfileImg setUserImage={setUserImage} />
      </S.ImgWrapper>
      <S.InputForm>
        <InputBox
          id="username"
          labelText=" "
          placeholder="2~10  ."
          onChange={(e) => {
            setUsername(e.target.value);
          }}
        />
        <S.ErrMsg className="message">{usernameError}</S.ErrMsg>
        <InputBox
          id="accountname"
          labelText=" ID"
          placeholder=", , (.),(_)  ."
          onChange={(e) => {
            setAccountname(e.target.value);
          }}
        />
        <S.ErrMsg className="message">{accountnameError}</S.ErrMsg>
        <InputBox
          id="itemIntro"
          labelText=""
          placeholder="     !"
        />
      </S.InputForm>
      <LongBtn
        message="triplus "
        onClick={signInHandler}
        disabled={passed ? false : true}
      ></LongBtn>
    </S.FormContainer>
  );
};

export default SetProfile;
