import React, { useState, useEffect } from "react";
import InputBox from "../../components/InputBox/index";
import LongBtn from "../../components/Button/LongBtn";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import * as S from "./style";

export default function EmailSignUp(props) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [emailValid, setEmailValid] = useState(false);
  const [passwordValid, setPasswordValid] = useState(false);
  const navigate = useNavigate();

  /*       */
  useEffect(() => {
    const emailRegex =
      /^([A-Z|a-z|0-9](\-|\_){0,1})+[A-Z|a-z|0-9]\@([A-Z|a-z|0-9])+((\.){0,1}[A-Z|a-z|0-9]){2}\.[a-z]{2,3}$/;

    if (!emailRegex.test(email) && email !== "") {
      setEmailError("*    .");
      setEmailValid(false);
    } else if (email === "") {
      setEmailError("*  ");
      setEmailValid(false);
    } else {
      setEmailError("");
      setEmailValid(true);
    }
  }, [email]);

  /*        */
  useEffect(() => {
    if (password.length < 6 && password !== "") {
      setPasswordError("*  6  .");
      setPasswordValid(false);
    } else if (password == "") {
      setPasswordError("*  .");
      setPasswordValid(false);
    } else {
      setPasswordError("");
      setPasswordValid(true);
    }
  }, [password]);

  /*     post */
  const sendData = async () => {
    const res = await axios.post(
      `${process.env.REACT_APP_API_KEY}/user/emailvalid`,
      {
        user: {
          email: email,
        },
      }
    );

    if (res.data.message === "   .") {
      setEmailValid(true);
      navigate("/setprofile", {
        state: {
          email: email,
          password: password,
        },
      });
    } else if (res.data.message === "    .") {
      setEmailError("*   .");
      setEmailValid(false);
    } else {
      setEmailValid(false);
      alert("   ");
    }
  };

  return (
    <S.FormContainer>
      <S.PageTitle className="ir">triplus  </S.PageTitle>
      <S.SignUpTitle> </S.SignUpTitle>
      <S.InputForm>
        <InputBox
          id="email"
          labelText=""
          placeholder="  "
          onChange={(e) => {
            setEmail(e.target.value);
          }}
        />
        <S.ErrMsg className="message">{emailError}</S.ErrMsg>
        <InputBox
          type="password"
          id="password"
          labelText=""
          placeholder=" "
          onChange={(e) => {
            setPassword(e.target.value);
          }}
        />
        <S.ErrMsg className="message">{passwordError}</S.ErrMsg>
      </S.InputForm>
      <LongBtn
        disabled={emailValid && passwordValid ? false : true}
        onClick={sendData}
        message=""
      ></LongBtn>
    </S.FormContainer>
  );
}
